import React from "react";
/* import { Link } from "react-router-dom"; */
import "../App.css";

function Thumbnail(props) {
  return (
    <div>
      {/*  <Link to=""> */}
      <div className="shot">
        <a href={props.link} target="_blank">
          {" "}
          <img src={props.image} alt="Project Image" />
        </a>
      </div>

      {/* </Link> */}
    </div>
  );
}

export default Thumbnail;
