import React from "react";
/* import { Link } from "react-router-dom"; */
import "../App.css";

function Logos(props) {
  return (
    <div>
      {/*  <Link to=""> */}
      <div className="logo">
        <a href={props.link} target="_blank">
          {" "}
          <img src={props.logos} alt="Logo Image" />
        </a>
      </div>

      {/* </Link> */}
    </div>
  );
}

export default Logos;
